	data() {
		return {
			title: '',
			content: '',
			userId: uni.getStorageSync('login_key').userId,
			imgs: [],
			show: false
		};
	},
	onLoad() {},
	methods: {
		goBack: function() {
			uni.navigateBack({});
		},
		changeYL: function() {
			this.show = !this.show;
		},
		chooseImg: function() {
			var _this = this;
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'],
				sourceType: ['album'],
				success: function(res) {
					console.log(JSON.stringify(res.tempFilePaths));
					uni.uploadFile({
						url: _this.apiServer + '/avatar/upload',
						filePath: res.tempFilePaths[0],
						name: 'file',
						success: uploadFileRes => {
							//图片上传成功，回显图片地址
							console.log(uploadFileRes.data);
							//将图片地址加入imgs数组
							_this.imgs.push(uploadFileRes.data);
							//将图片地址拼接HTML标签，加入文章内容
							_this.content += '<img src="' + uploadFileRes.data + '" width = "100%"/>';
						}
					});
				}
			});
		},
		postArticle: function() {
			var _this = this;
			uni.request({
				url: this.apiServer + '/article/add',
				method: 'POST',
				header: { 'content-type': 'application/x-www-form-urlencoded' },
				data: {
					uId: this.userId,
					title: this.title,
					content: '<div>' + this.content + '</div>'
				},
				success: res => {
					if (res.data.code === 0) {
						//获得发布文章成功返回的文章id
						var aId = res.data.data;
						console.log(aId);
						uni.showToast({
							title: '简阅钻+10'
						});
						//将文章id和文章对应的图片地址数组传到后台，存入数据库
						uni.request({
							url: 'http://47.101.34.195:8080/api/img/add',
							method: 'POST',
							header: { 'content-type': 'application/x-www-form-urlencoded' },
							data: {
								aId: aId,
								imgs: JSON.stringify(_this.imgs) //序列化imgs数组
							},
							success: res => {
								if (res.data.code === 0) {
									console.log('文章图片地址已写入数据库');
								}
							}
						});
						uni.switchTab({
							url: '../index/index'
						});
					}
				},
				complete:function(){
					var newScore=uni.getStorageSync('login_key').score+10;
					uni.request({
						url: 'http://47.101.34.195:8080/api/user/score',
						method: 'post',
						header: { 'content-type': 'application/x-www-form-urlencoded' },
						data: {
							userId: _this.userId,
							score: newScore
						},
						success: resd => {
							console.log(resd.data);
							this.promptVisible = false;
							uni.request({
								url: 'http://47.101.34.195:8080/api/user/' + uni.getStorageSync('login_key').userId,
								method: 'GET',
								data: {
									userId: _this.userId
								},
								header: {
									'content-type': 'application/json'
								},
								success: res => {
									if (res.data.code == 0) {
										//将用户数据记录在本地存储
										uni.setStorageSync('login_key', {
											userId: res.data.data.id,
											nickname: res.data.data.nickname,
											avatar: res.data.data.avatar,
											token: res.data.data.token,
											score:res.data.data.score,
											mobile: res.data.data.mobile,
											login: true
										});
									}
								}
							});
						}
					});
				}
			});
		}
	}
};
